import { Routes, RouterModule }  from '@angular/router';
import { Layout } from './layout.component';
// noinspection TypeScriptValidateTypes
const routes: Routes = [
  { path: '', component: Layout, children: [
    { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
    { path: 'dashboard', loadChildren: () => System.import('../dashboard/dashboard.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
	{ path: 'frontpage', loadChildren: () => System.import('../frontpage/frontpage.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'inbox', loadChildren: () => System.import('../inbox/inbox.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'charts', loadChildren: () => System.import('../charts/charts.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'profile', loadChildren: () => System.import('../profile/profile.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'forms', loadChildren: () => System.import('../forms/forms.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'ui', loadChildren: () => System.import('../ui-elements/ui-elements.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'extra', loadChildren: () => System.import('../extra/extra.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'tables', loadChildren: () => System.import('../tables/tables.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'maps', loadChildren: () => System.import('../maps/maps.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'grid', loadChildren: () => System.import('../grid/grid.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'charts', loadChildren: () => System.import('../charts/charts.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'widgets', loadChildren: () => System.import('../widgets/widgets.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'merchant', loadChildren: () => System.import('../merchant/merchant.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
    { path: 'Transactions', loadChildren: () => System.import('../transactions/transactions.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
     { path: 'Profitconfiguration', loadChildren: () => System.import('../profitconfiguration/profitconfiguration.module').then(mod => (mod.__esModule && mod.default) ? mod.default : mod) },
  ]}
];

export const ROUTES = RouterModule.forChild(routes);



